.nav {
	display: flex;
	flex-direction: column;
	/* flex-grow: 1; */
	flex: 0 1 15%;
	border-right: 2px solid rgba(168, 168, 168, 0.4);
	padding: 130px 0 20px 0;
}

.item a {
	color: rgb(18, 33, 93);
	text-decoration: none;
	font-size: 20px;
	letter-spacing: 1px;
	padding: 0 10px 0 40px;
}

.item:not(:last-child) {
	margin-bottom: 8px;
}

a {
	&.active {
		color: gold;
	}
}
