.header {
	width: 100%;
	background-color: #2b3c55;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 99;
	box-shadow: 0px 5px 5px 5px rgba(43, 60, 85, 0.3);
	/* margin-bottom: 30px; */
}

.container {
	display: flex;
	min-height: 100px;
	align-items: center;
}
.logo {
	padding-left: 20px;
}
.logo img {
	border-radius: 50%;
	width: 80px;
	background-color: transparent;
}

.login {
	display: flex;
	margin: 0 0 0 auto;
}

.login a {
	color: blue;
	// font-size: 20px;
}
